
.audio{position:relative;background-color:#f7f7f7;
}
.audio img{width:25px;
}
.audio .audio_one{position: absolute;top:10px;right:15px;
}
.audio .audio_two{position: absolute;top:10px;right:70px;
}
.audio ul{width:100%;height:39px;border-bottom:1px solid #ececec;background-color:#fff;
}
.audio ul li{height:37px;width:20%;float:left;border-bottom:2px solid #fff;line-height:37px;list-style:none;
}
.audio ul li a{color:#666;text-decoration:none;font-size:14px;
}
.audio ul .active{border-bottom-color:#64ca80;
}
.audio ul .active a{color:#64ca80;
}



