
.bcast{position:relative;background-color:#f7f7f7;
}
.bcast img{width:25px;
}
.bcast .bcast_one{position: absolute;top:10px;right:15px;
}
.bcast .bcast_two{position: absolute;top:10px;right:70px;
}
.bcast .find{text-align:left;
}
.bcast .find img{width:90px;float:left;margin:-17px 20px 0 50px;
}
.bcast .find h3{height:24px;line-height:24px;margin-top:37px;font-size:16px;
}
.bcast .find p{height:20px;line-height:20px;font-size:12px;color:#666;padding-bottom:34px;
}



