
.newsWrap{width:335px;padding:16px 20px;border-bottom:1px solid #f1f1f1;background-color:#fff;
}
.clearfix::after{content:"200B";clear:both;font-size:0;height:0;display:block;
}
.newsWrap .text{width:225px;margin-right:20px;float:left;
}
.newsWrap .pic{float:left;font-size:0;
}
.newsWrap .pic img{width:90px;
}
.newsWrap .text h3{font-size:18px;line-height:25px;
}
.newsWrap .text p{font-size:12px;line-height:20px;margin-top:7px;
}
.newsWrap h6{font-size:12px;color: #ccc;height:16px;line-height:16px;
}
.newsWrap .writer{color:#ccc;font-size:12px;height:20px;line-height:20px;
}
.newsWrap .writer .right{float:right;
}



