
.remindWrap{margin-top:10px;background-color:#fff;margin-bottom:15px;
}
.remindWrap .re_pic{text-align:left;height:50px;line-height:50px;
}
.remindWrap .re_pic img{margin-left:20px;vertical-align:middle;
}
.remindWrap .re_pic span{font-size:14px;font-weight:bold;color:#000;
}
.remindWrap .re_pic a{float:right;text-decoration:none;color:#666;font-family:consolas;margin-right:15px;
}
.remindWrap .remind_list{height:40px;line-height:40px;font-size:12px;color:#ccc;border-top:1px solid #ccc;
}



