
.group{position:relative;background-color:#f7f7f7;
}
.group img{width:25px;
}
.group .group_one{position: absolute;top:10px;right:15px;
}
.group .group_two{position: absolute;top:10px;right:70px;
}
.group .joinUs img{width:100%;
}
.group .joinUs{position:relative;font-size:0;
}
.group .joinUs h2{position:absolute;top:59px;left:88px;height:23px;line-height:23px;font-size:18px;
}
.group .joinUs p{position:absolute;top:82px;left:134px;height:19px;line-height:19px;font-size:14px;color:#acb1af;
}
.group .joinUs h6{position:absolute;bottom:3px;left:15px;font-size:12px;font-weight:normal;color:#acb1af;
}
.group .star{padding:20px 0 25px 0;background-color:#fff;
}
.group .star img{width:90%;
}



