
.shaixuanWrap{height:49px;-webkit-box-shadow:0 -3px 5px 0 #eee;box-shadow:0 -3px 5px 0 #eee;line-height:49px;width:100%;position:fixed;bottom:49px;left:0;text-align:left;background-color:#fff;z-index:3;
}
.shaixuanWrap a{display:inline-block;float:right;margin-right:15px;width:140px;height:28px;background-color:#dfdfdf;border-radius:3px;line-height:28px;text-decoration:none;color:#fff;font-size:12px;text-align:center;margin-top:11px;
}
.shaixuanWrap h4{padding-left:25px;font-size:12px;color:#42c055;
}
.shaixuanWrap img{width:22px;vertical-align:middle;
}



